import React, { useState } from 'react';
import jsPDF from 'jspdf';
import 'jspdf-autotable';

function App() {
  const [seccion, setSeccion] = useState('bienvenida');
  const [tipoCliente, setTipoCliente] = useState(null);
  const [maquinas, setMaquinas] = useState([]);
  const [nuevaMaq, setNuevaMaq] = useState({
    marca: '', modelo: '', serie: '', dueo: '', horas: '', trabajosRealizados: '', trabajosPendientes: '', telefono: ''
  });

  const enviarFicha = (m) => {
    try {
      const doc = new jsPDF();
      doc.setFontSize(18);
      doc.text("HIDRULICA RECONQUISTA - FICHA TCNICA", 14, 20);
      
      const filas = [
        ['DUEO / EMPRESA', m.dueo],
        ['MAQUINARIA', m.marca + " " + m.modelo],
        ['NRO DE SERIE', m.serie],
        ['HORAS DE USO', m.horas],
        ['TRABAJOS REALIZADOS', m.trabajosRealizados],
        ['TRABAJOS PENDIENTES', m.trabajosPendientes]
      ];

      doc.autoTable({
        startY: 30,
        head: [['CONCEPTO', 'DETALLE']],
        body: filas,
        theme: 'striped',
        headStyles: { fillColor: [255, 215, 0], textColor: [0, 0, 0] }
      });

      // 1. DESCARGA EL PDF
      doc.save("Ficha_" + m.marca + ".pdf");

      // 2. LIMPIEZA DE TELFONO (QUITAMOS TODO LO QUE NO SEA NMERO)
      const numSoloNumeros = m.telefono.replace(/\D/g, '');
      
      // 3. ARMADO DEL MENSAJE (CON ORTOGRAFA CORREGIDA)
      const textoMensaje = "Hola " + m.dueo + ", le envo la ficha tcnica de la " + m.marca + " " + m.modelo + ". (Adjunte el PDF que se acaba de descargar)";
      
      // 4. LINK DE WHATSAPP (CORREGIDO: AGREGADO EL '?' QUE FALTABA)
      const urlFinal = "https://api.whatsapp.com" + numSoloNumeros + "&text=" + encodeURIComponent(textoMensaje);
      
      setTimeout(() => {
        if(window.confirm("Ficha tcnica descargada. Desea abrir WhatsApp ahora?")) {
           window.open(urlFinal, '_blank');
        }
      }, 1000);

    } catch (error) {
      alert("Error al generar el archivo. Revise los datos.");
    }
  };

  const guardar = (e) => {
    e.preventDefault();
    const listaActualizada = [...maquinas, { ...nuevaMaq, id: Date.now(), tipo: tipoCliente }];
    listaActualizada.sort((a, b) => a.marca.localeCompare(b.marca));
    setMaquinas(listaActualizada);
    setSeccion('listado');
    setNuevaMaq({ marca: '', modelo: '', serie: '', dueo: '', horas: '', trabajosRealizados: '', trabajosPendientes: '', telefono: '' });
  };

  if (seccion === 'bienvenida') return (
    <div style={styles.pantallaFull}>
      <h1 style={styles.tituloOro}>HIDRULICA RECONQUISTA</h1>
      <button style={styles.btnPrincipal} onClick={() => setSeccion('menu')}>INGRESAR AL SISTEMA</button>
    </div>
  );

  if (seccion === 'formulario') return (
    <div style={styles.contenedor}>
      <h2 style={{color: '#ffd700', textAlign:'center'}}>Registro: {tipoCliente}</h2>
      <form onSubmit={guardar} style={styles.formulario}>
        <input placeholder="Marca" value={nuevaMaq.marca} onChange={e => setNuevaMaq({...nuevaMaq, marca: e.target.value})} required style={styles.input}/>
        <input placeholder="Modelo" value={nuevaMaq.modelo} onChange={e => setNuevaMaq({...nuevaMaq, modelo: e.target.value})} required style={styles.input}/>
        <input placeholder="WhatsApp (Ej: 5493482446010)" value={nuevaMaq.telefono} onChange={e => setNuevaMaq({...nuevaMaq, telefono: e.target.value})} required style={styles.input}/>
        <input placeholder="Dueo" value={nuevaMaq.dueo} onChange={e => setNuevaMaq({...nuevaMaq, dueo: e.target.value})} style={styles.input}/>
        <input placeholder="Nro de Serie" value={nuevaMaq.serie} onChange={e => setNuevaMaq({...nuevaMaq, serie: e.target.value})} style={styles.input}/>
        <input placeholder="Horas" value={nuevaMaq.horas} onChange={e => setNuevaMaq({...nuevaMaq, horas: e.target.value})} style={styles.input}/>
        <textarea placeholder="Trabajos Realizados" value={nuevaMaq.trabajosRealizados} onChange={e => setNuevaMaq({...nuevaMaq, trabajosRealizados: e.target.value})} style={styles.area}/>
        <textarea placeholder="Trabajos Pendientes" value={nuevaMaq.trabajosPendientes} onChange={e => setNuevaMaq({...nuevaMaq, trabajosPendientes: e.target.value})} style={styles.area}/>
        <button type="submit" style={styles.btnPrincipal}>GUARDAR FICHA</button>
        <button type="button" onClick={() => setSeccion('menu')} style={styles.btnGris}>CANCELAR</button>
      </form>
    </div>
  );

  if (seccion === 'listado') return (
    <div style={styles.contenedor}>
      <h1 style={styles.tituloOro}>INVENTARIO</h1>
      {maquinas.map(m => (
        <div key={m.id} style={styles.tarjeta}>
          <h3 style={{margin: '0'}}>{m.marca.toUpperCase()} - {m.modelo}</h3>
          <p><strong>Dueo:</strong> {m.dueo}</p>
          <button onClick={() => enviarFicha(m)} style={styles.btnVerde}> PDF Y WHATSAPP</button>
        </div>
      ))}
      <button onClick={() => setSeccion('menu')} style={styles.btnGris}>VOLVER</button>
    </div>
  );

  return (
    <div style={styles.contenedor}>
      <div style={styles.gridBotones}>
        <button style={styles.btnPrincipal} onClick={() => {setTipoCliente('Nuevo'); setSeccion('formulario')}}>CLIENTE NUEVO</button>
        <button style={styles.btnPrincipal} onClick={() => {setTipoCliente('Corriente'); setSeccion('formulario')}}>CLIENTE CORRIENTE</button>
        <button style={{...styles.btnPrincipal, backgroundColor: '#444'}} onClick={() => setSeccion('listado')}>VER INVENTARIO</button>
      </div>
    </div>
  );
}

const styles = {
  pantallaFull: { height: '100vh', display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center', backgroundColor: '#000' },
  tituloOro: { color: '#ffd700', fontSize: '2.2rem', textAlign: 'center', marginBottom: '20px' },
  contenedor: { padding: '20px', backgroundColor: '#1a1a1a', minHeight: '100vh', color: 'white', fontFamily: 'Arial' },
  gridBotones: { display: 'grid', gap: '15px', maxWidth: '400px', margin: '0 auto' },
  btnPrincipal: { padding: '18px', backgroundColor: '#ffd700', color: 'black', fontWeight: 'bold', border: 'none', borderRadius: '8px', cursor: 'pointer' },
  formulario: { display: 'flex', flexDirection: 'column', gap: '10px', maxWidth: '500px', margin: '0 auto' },
  input: { padding: '12px', backgroundColor: '#2a2a2a', color: 'white', border: '1px solid #444', borderRadius: '5px' },
  area: { padding: '12px', backgroundColor: '#2a2a2a', color: 'white', border: '1px solid #444', borderRadius: '5px', minHeight: '80px' },
  btnGris: { padding: '12px', backgroundColor: '#555', color: 'white', border: 'none', borderRadius: '5px', cursor: 'pointer', marginTop: '10px' },
  tarjeta: { backgroundColor: '#2a2a2a', padding: '20px', borderRadius: '12px', marginBottom: '15px', borderLeft: '8px solid #ffd700' },
  btnVerde: { padding: '12px', backgroundColor: '#25D366', color: 'white', fontWeight: 'bold', border: 'none', borderRadius: '5px', cursor: 'pointer', marginTop: '10px' }
};

export default App;